import { Formik, Form } from 'formik'
import * as yup from 'yup'
import { Link } from 'react-router-dom'
import useAuth from 'hooks/useAuth'
import useAuthService from 'hooks/useAuthService'
import useImagesService from 'hooks/useImagesService'
import { useNavigate } from 'react-router-dom'

import Container from 'components/Container'
import Input from "components/form/Input"
import PasswordInput from 'components/form/PasswordInput'
import Button from 'components/Button'
import Icon from 'components/Icon'
import IsLoginAvailable from 'components/form/IsLoginAvailable'


const initialValues = {
    name: '',
    login: '',
    password: '',
    passwordConfirmation: ''
}

const validationSchema = yup.object({
    name: yup
        .string()
        .min(3, '     3- ')
        .max(20, '     20- ')
        .required(' '),
    login: yup
        .string()
        .min(3, '     3- ')
        .max(30, '     30- ')
        .required(' '),
    password: yup
        .string()
        .min(3, '     3- ')
        .max(60, '     60- ')
        .required(' '),
    passwordConfirmation: yup
        .string()
        .oneOf([ yup.ref('password'), null ], '  ')
})

const RegisterForm = () => {
    const { setAuth } = useAuth()
    const { register } = useAuthService()
    const { getImage } = useImagesService()
    const navigate = useNavigate()

    
    const handleSubmit = async (values, actions) => {    
        const newUser = [
            values.name,
            values.login,
            values.password
        ]
    
        try {
            const data = await register(...newUser)

            const accessToken = data.jwt
            const user = data.users[0]

            const ava = await getImage(user.thumbnail)

            setAuth({ user: { ...user, ava }, jwt: accessToken })

            actions.resetForm()
            navigate('/', { replace: true })
        } catch (error) {
            console.log(error)
        } finally {
            actions.setSubmitting(false)
        }
    }

    return (
        <div className="grid items-center h-full">
            <Container>
                <Icon 
                    className="m-auto block mb-2"
                    name="logo"
                    width="72"
                    height="32" />
                <div className="text-gray-500 text-center mb-8"></div>

                <Formik 
                    initialValues={initialValues}
                    validationSchema={validationSchema}
                    validateOnBlur={false}
                    onSubmit={handleSubmit}
                >
                    <Form className="mb-8">
                        <Input 
                            className="mb-6" 
                            name="name" 
                            label=""
                            autoComplete="given-name"
                        />  
                        <Input 
                            className="mb-6" 
                            name="login" 
                            label=""
                            autoComplete="username"
                        />
                        <IsLoginAvailable />
                        <PasswordInput 
                            className="mb-6" 
                            name="password" 
                            label=""
                            autoComplete="new-password"
                        />
                        <PasswordInput 
                            className="mb-8" 
                            name="passwordConfirmation" 
                            label="  "
                            autoComplete="off"
                        />
                        <Button className="w-full" type="submit"></Button>
                    </Form>
                </Formik>
                <div className="text-center text-gray-500 text-sm">
                      ?
                    <Link className="text-primary-700" to="../login"> </Link>
                </div>
            </Container>
        </div>
    )
}

export default RegisterForm