import * as yup from 'yup'
import { Formik, Form } from 'formik'
import usePointsService from 'hooks/usePointsService'

import Input from "components/form/Input"
import Button from "components/Button"
import Modal from './Modal'
import SmallTitle from 'components/SmallTitle'

const CreateModal = ({ handleHideModal, setLocations }) => {
    const { createPoint } = usePointsService()

    return (
        <Modal className="text-center" hideModal={handleHideModal}>
            <SmallTitle 
                className="mb-12"
                title=""
            />
            <Formik
                initialValues={{ 
                    title: '',
                    lng: '',
                    lat: '',
                    code: ''
                }}
                validationSchema={yup.object({
                    title: yup.string().required(' '),
                    lng: yup.string().required(' '),
                    lat: yup.string().required(' '),
                    code: yup.string().required(' '),
                })}
                onSubmit={async (values, actions) => {
                    try {
                        const point = {
                            title: values.title,
                            location: {
                                lat: values.lat,
                                lng: values.lng
                            },
                            code: values.code
                        }
                        const data = await createPoint(point)
                        handleHideModal()
                        setLocations(prev => [ ...prev, data.points[0] ])
                    } catch (error) {
                        console.log(error)
                    } finally {
                        actions.setSubmitting(false)
                    }
                }}
            >
                <Form>
                    <Input
                        label=""
                        name="title"
                    />
                    <Input     
                        label=""
                        name="lng"
                    />
                    <Input  
                        label=""
                        name="lat"
                    />
                    <Input
                        label=""
                        name="code"
                    />
                    <Button className="w-full" type="submit"></Button>
                </Form>
            </Formik>
        </Modal>
    )
}

export default CreateModal